<template>
	<view class="content" :style="{'height':screenHeight}">
		<view class="none"></view>
		<view class="topBox">
			<view class="box" v-for="(item,index) in topData" :key="index">
				<image class="icon" :src="item.src" mode="" :style=" index == 2 ? {'width':'50upx'} : '' "></image>
				<view class="name">{{item.type}}</view>
				<view class="names" v-if="index !== 2">{{item.nums}}人</view>
				<view class="names" v-if="index == 2">￥ {{item.nums}}</view>
			</view>
			<view class="clearBoth"></view>
		</view>
		<view class="rowsData">
			<view class="boxData" v-for="(item,index) in rowsDate" :key="index">
				<image class="headImage" :src="item.src" mode=""></image>
				<view class="right">
					<view class="name">
						<view class="nameNew overflow_one">{{item.name}}</view>
					</view>
					<view class="clearBoth"></view>
					<view class="names">
						<view class="nameNew overflow_one color_black">{{item.types}}级下级数</view>
						<view class="news fontSize12">区域下级</view>
					</view>
					<view class="clearBoth"></view>
					<view class="nums color_black">总返利：￥ {{item.money}}</view>
				</view>
				<view class="clearBoth"></view>
			</view>
		</view>
		<view class="clearBoth"></view>
		
		
		
		
	</view>
</template>
<style>
	.content .rowsData{width:100%;background: rgb(244,244,244);margin-top: 20upx;}
	.content .rowsData .boxData{width:90%;padding:4% 2.5%;margin:0upx 2.5%;;margin-top: 10upx;background: white;border-radius: 10upx;}
	.content .rowsData .boxData .headImage{width:15%;height:100upx;border-radius: 120upx;float: left;margin-right: 5%;}
	.content .rowsData .boxData .right{width:80%;float: left;}
	.content .rowsData .boxData .right .name{font-size: 12px;color:#8E8E8E;width:100%;float: left;}
	.content .rowsData .boxData .right .nameNew{font-size: 12px;color:#8E8E8E;max-width:80%;float: left;}
	.content .rowsData .boxData .right .news{font-size: 12px;float: left;width:22%;margin-left:2%;color:#4FB0F4;border:1px solid #4FB0F4;text-align: center;}
	.content .rowsData .boxData .right .names{width:100%;font-size:12px;color:#1A1A1A;margin-top:5upx}
	.content .rowsData .boxData .right .nums{color:#1A1A1A;font-size: 12px;}
	.color_black{color:black !important}
	.content{width:100%;background: rgb(244,244,244);padding-bottom: 20upx;}
	.content .none{width:100%;height:30upx;background: rgb(244,244,244)}
	.content .topBox{width:90%;padding:2.5%;margin:0upx 2.5%;background: white;border-radius: 10upx;}
	.content .topBox .box{width:33.3%;float: left;text-align: center;}
	.content .topBox .box .icon{width:65upx;height:60upx;margin:20upx 0;}
	.content .topBox .box .name{font-size:12px;color:#1A1A1A;margin-bottom: 10upx;}
	.content .topBox .box .names{font-size: 15px;color:#3DB8FF}
</style>
<script>
	export default {
		data() {
			return {
				screenHeight:"",
				topData:[
					{'src':'../../../static/image/user/item/icon_4.png','type':'一级下级数','nums':'123'},
					{'src':'../../../static/image/user/item/icon_5.png','type':'二级下级数','nums':'123'},
					{'src':'../../../static/image/user/item/icon_6.png','type':'总收益','nums':'123'},
				],
				rowsDate:[
					// {'src':'../../../static/demo.jpg','name':'这是名字','types':'一','money':1},
					// {'src':'../../../static/demo.jpg','name':'这是名字这是名字这是名字这是名字这是名字这是名字这是名字这是名字','types':'一','money':2},
				],
				pages:1,
				_$token:""
				
			}
		},
		onLoad(){
			var that = this;
			const res = uni.getSystemInfoSync();
			this.screenHeight = res.windowHeight*2 + 'upx'
			this.getStorage();
			setTimeout(function(){
				that.AjaxLoad();
			},100)
		},
		onReachBottom(){
			var that = this;
			this.pages++;
			setTimeout(function(){
				that.AjaxLoad();
			},100)
		},
		methods: {
			// getStorage(){var that = this;uni.getStorage({ key: 'token',success: function (res) {that._$token = res.data}})},
			getStorage(){var that = this;try {const value = uni.getStorageSync('token');if (value) {that._$token = value}} catch (e) {}},
			AjaxLoad(){
				var that = this;
				var lists = []
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"page":this.pages
				};
				this._$InitRequest.request('User/team_list', param).then(res => {
					this.topData[0].nums = res.data.data.first_count
					this.topData[1].nums = res.data.data.second_count
					this.topData[2].nums = res.data.data.total_price
					res.data.data.user_data.forEach((item,index)=>{
						lists.push({
							id:item.user_id,
							src:item.headimgurl,
							name:item.nickname,
							money:item.total_price,
							types:item.type,
						})
					})
					for (var i = 0; i < lists.length; i++){
						that.rowsDate.push(lists[i])
					}
				},error=>{})
			},
			
		}
	}
</script>